class Notes {
  constructor() {
    const deleteNote = document.querySelectorAll(".delete-note");
    // console.log(deleteNote);
    this.events();
  }
  
  events() {
    let deleteBtn =  Array.from(this.deleteNote);
    deleteBtn.map(btn => {
      btn.addEventListener("click", deleteNote);
    })
  }

  // Methods
  deleteNote(e) {
    
      console.log(e);

  
  }
 }

 export default Notes;